import { FileInfo, ListSeparator, MenuItem } from '@limetech/lime-elements';
import { Component, h, State } from '@stencil/core';

/**
 * Custom menu items
 *
 */
@Component({
    tag: 'limel-example-file-menu-items',
    shadow: true,
})
export class FileMenuItemsExample {
    @State()
    private value: FileInfo = { filename: 'deal.pdf', id: 123 };

    @State()
    private menuItems: Array<MenuItem | ListSeparator> = [
        {
            text: 'Download',
            icon: 'download',
            value: 1,
        },
        {
            text: 'Share',
            icon: 'share',
            value: 2,
        },
    ];

    public render() {
        return (
            <limel-file
                label="Attach a file"
                onChange={this.handleChange}
                value={this.value}
                menuItems={this.menuItems}
            />
        );
    }

    private handleChange = (event: CustomEvent<FileInfo>) => {
        this.value = event.detail;
        console.log('onChange', this.value);
    };
}
